import styled from 'styled-components';

import usersIcon from 'assets/svg/users.svg';
import groupsIcon from 'assets/svg/groups.svg';
import ratingIcon from 'assets/svg/rating.svg';
import shopIcon from 'assets/svg/shop.svg';
import feedIcon from 'assets/svg/feed.svg';




const icons = {
  users: usersIcon,
  groups: groupsIcon,
  rating: ratingIcon,
  shop: shopIcon,
  feed: feedIcon,
}

const StyledIcon = styled.img`
  width: ${(props) => props.size}px;
`

export const Icon = ({ type, size=32 }) => {
  return <StyledIcon src={icons[type]} size={size} alt={type} />
}